export default "/* You can add global styles to this file, and also import other style files */\n/* ------------  Brand Colors  ---------------- */\n/* -----------  Border Colors  ---------------- */\n/* -----------  Button Colors  ---------------- */\n/* -----------  Font Colors  ------------------- */\n/* -----------  Input Group Colors  ------------ */\n/* -----------  Page Layout Colors  ------------ */\n/* -----------  Table Colors  ------------------ */\n/* -----------  Font Settings  ------------------- */\n/* -----------  Button Settings  ----------------- */\n/* -----------  PC Settings  ----------------- */\nh1, h2, h3, h4, h5, h6, header, section, p, a, b, title, textarea, input {\n  margin: 0;\n  padding: 0;\n  font-family: \"Nunito Sans\", \"sans-serif\";\n}\ndiv:focus, header:focus, button:focus {\n  outline: none;\n}\n.pagecontainer {\n  padding: 25px;\n  background: #F2F2F2;\n  min-height: calc(100vh - 70px - 50px);\n}\n.pagecontainer .container {\n  margin-top: 25px;\n  background: white;\n  padding: 25px;\n}\n.pagecontainer .container > header {\n  display: flex;\n  justify-content: space-between;\n  margin-bottom: 20px;\n}\n.pagecontainer .container > header .left, .pagecontainer .container > header .right {\n  display: flex;\n  align-items: center;\n}\n/* Sample Code for Page Container */\n/* ----------------------------------------------------------\n<div class=\"pagecontainer\">\n    <!--App Loader-->\n    <app-loader *ngIf=\"loader_params.isdisplayed\" (close)=\"toggleLoader($event)\"></app-loader>\n\n    <!--App Message-->\n    <app-message *ngIf=\"message_params.isdisplayed\" [is_message_displayed]=\"message_params\"></app-message>\n\n    <!--Sample Page Section 1-->\n    <div class=\"pagesection\">\n        <header>Buttons</header>\n        <div class=\"button-container\">\n            <div class=\"left\">\n                <button (click)=\"toggleModal()\">Show Modal</button>\n                <button (click)=\"toggleLoader()\">Toggle Loader</button>\n            </div>\n            <div class=\"right\">\n                <button (click)=\"toggleMessage()\">Toggle Message</button>\n            </div>\n        </div>\n    </div>\n\n    <!--Sample Page Section 2-->\n    <div class=\"pagesection\">\n\n    </div>\n</div>\n\n ---------------------------------------------------------- */\n.button-container {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 20px;\n}\n.button-container .left, .button-container .right {\n  display: flex;\n  align-items: center;\n}\n.button-container .left {\n  justify-content: flex-start;\n}\n.button-container .left i, .button-container .left header, .button-container .left button, .button-container .left img {\n  margin-right: 10px;\n}\n.button-container .right {\n  justify-content: flex-end;\n}\n.button-container .right i, .button-container .right header, .button-container .right button, .button-container .right img {\n  margin-left: 10px;\n}\n/* Sample Code for Button Container */\n/* ----------------------------------------------------------\n<div class=\"button-container\">\n    <div class=\"left\">\n        <button (click)=\"toggleModal()\">Show Modal</button>\n        <button (click)=\"toggleLoader()\">Toggle Loader</button>\n    </div>\n    <div class=\"right\">\n        <button (click)=\"toggleMessage()\">Toggle Message</button>\n    </div>\n</div>\n ---------------------------------------------------------- */\n.table {\n  font-size: 13px;\n  color: #212529;\n}\n.table .column {\n  width: 18%;\n  padding: 1%;\n}\n.table .column-s {\n  width: 3%;\n  padding: 1%;\n}\n.table .column-m {\n  width: 8%;\n  padding: 1%;\n}\n.table .column-l {\n  width: 13%;\n  padding: 1%;\n}\n.table .column-xl {\n  width: 18%;\n  padding: 1%;\n}\n.table .row {\n  display: flex;\n  align-items: center;\n  min-height: 30px;\n}\n.table .table-header {\n  background: #f2f2f2;\n  font-weight: 700;\n}\n.table .table-data {\n  max-height: 360px;\n  overflow-y: auto;\n}\n.table .table-data .row {\n  border-bottom: 1px solid #d0dee6;\n}\n/* Sample Code for Table */\n/* ----------------------------------------------------------\n<div class=\"table\">\n    <div class=\"row table-header\">\n        <div class=\"column\">\n            #\n        </div>\n        <div class=\"column\">\n            Name\n        </div>\n    </div>\n    <div class=\"table-data\">\n        <div class=\"row\" *ngFor=\"let i of [1,2,3];let index = index\">\n            <div class=\"column\">\n                {{index + 1}}\n            </div>\n            <div class=\"column\">\n                {{'Name' + index}}\n            </div>\n        </div>\n    </div>\n</div>\n ---------------------------------------------------------- */\n/* Buttons */\nbutton {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  min-height: 34px;\n  padding: 8px 15px;\n  font-family: \"Nunito Sans\", \"sans-serif\";\n  font-size: 14px;\n  text-decoration: none;\n  font-weight: 400;\n  color: #FFF;\n  border: 1px solid #ff9e00;\n  border-radius: 3px;\n  transition: 0.3s;\n  background-color: #ff9e00;\n  background-image: #ff9e00;\n  filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0, startColorstr=#498493, endColorstr=#48778A);\n  cursor: pointer;\n}\nbutton i {\n  margin: 0 5px;\n}\nbutton span {\n  margin: 0 5px;\n}\nbutton:hover {\n  transition: 0.5s;\n}\nbutton:focus {\n  outline: none;\n}\nbutton.outline {\n  background: white;\n  color: #ff9e00;\n  border: 1px solid #ff9e00;\n}\nbutton.nooutline {\n  background: white;\n  color: #000;\n  border: none;\n}\nbutton.disabled {\n  opacity: 0.5;\n  pointer-events: none;\n}\n/* Scroll Bars */\n/* width */\n::-webkit-scrollbar {\n  width: 6px;\n}\n/* Track */\n::-webkit-scrollbar-track {\n  background: #f1f1f1;\n}\n/* Handle */\n::-webkit-scrollbar-thumb {\n  background: #888;\n  border-radius: 4px;\n}\n/* Handle on hover */\n::-webkit-scrollbar-thumb:hover {\n  background: #555;\n}\ninput {\n  padding: 3px 7px;\n  border: 1px solid #000;\n}\ninput:focus {\n  outline: none;\n}\n/* Inputs */\n.ig {\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  flex-wrap: wrap;\n  width: 100%;\n}\n.ig .ig-item {\n  min-height: 36px;\n  padding: 5px 0;\n  cursor: text;\n  margin-bottom: 15px;\n}\n.ig .ig-item .ig-item-header {\n  font-weight: 700;\n  font-size: 13px;\n  margin-bottom: 5px;\n  color: #53565A;\n}\n.ig .ig-item .ig-input {\n  border-left: 4px solid white;\n}\n.ig .ig-item .ig-input > header {\n  font-size: 14px;\n  font-weight: 400;\n  color: #53565A;\n  margin-left: -4px;\n  min-height: 38px;\n}\n.ig .ig-item .ig-input > i {\n  border-top: 1px solid #CFDDE6;\n  border-bottom: 1px solid #CFDDE6;\n  height: 37px;\n  display: flex;\n  align-items: center;\n  padding-left: 10px;\n  padding-right: 5px;\n}\n.ig .ig-item .ig-input input {\n  border: 1px solid #CFDDE6;\n  outline: none;\n  font-size: 14px;\n  padding: 9px 15px;\n  width: calc(100% - 32px);\n  margin-left: -4px;\n}\n.ig .ig-item .ig-input input::-webkit-input-placeholder {\n  color: #98A4AE;\n  font-size: 14px;\n}\n.ig .ig-item .ig-input input::-moz-placeholder {\n  color: #98A4AE;\n  font-size: 14px;\n}\n.ig .ig-item .ig-input input::-ms-input-placeholder {\n  color: #98A4AE;\n  font-size: 14px;\n}\n.ig .ig-item .ig-input input::placeholder {\n  color: #98A4AE;\n  font-size: 14px;\n}\n.ig .ig-item .ig-input textarea {\n  font-size: 14px;\n  width: calc(100% - 22px);\n  height: 100px;\n  border: 1px solid #CFDDE6;\n  padding: 10px;\n  margin-left: -4px;\n}\n.ig .ig-item .ig-input .dropdown, .ig .ig-item .ig-input .default, .ig .ig-item .ig-input .material {\n  border: 1px solid #CFDDE6;\n  margin-left: -4px;\n}\n.ig .ig-item .ig-input .dropdown .dropdown-button, .ig .ig-item .ig-input .default .dropdown-button, .ig .ig-item .ig-input .material .dropdown-button {\n  border: none;\n}\n.ig .ig-item .ig-input-required {\n  border-left: 4px solid #ED8B00;\n}\n.ig .ig-item .